* {
  box-sizing: border-box ;
}

body {
  margin: 0 ;
  font-family: Arial, Helvetica, sans-serif ;
}

.header {
  width: 100% ;
  padding: 5px 15px ;
  background: #730082 ;
  color: #FFF ;
  font-size: 30px ;
}

.main {
  position: relative ;
  margin-top: 10px ;
}

.folder,
.file {
  display: flex ;
  align-items: center ;
  margin-bottom: 10px ;
  padding: 5px 25px ;
  width: max-content ;
  border-radius: 5px ;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.46) ;
  cursor: pointer ;
  height: 50px ;
}

.folder {
  background: #A7A8A8 ;
}

.folder h1 {
  margin-left: 10px ;
  margin-right: 10px ;
}

.folder svg:nth-of-type(2),
.folder svg:nth-of-type(3),
.file h1 {
  margin-right: 10px ;
}

.folder--input {
  outline: none ;
  border: none ;
  border-radius: 5px ;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.46) ;
  padding: 7px 10px ;
}

.file {
  background: #C7C8C9 ;
}

.editor {
  position: absolute ;
  top: 0 ;
  bottom: 0 ;
  right: 0 ;
  width: 50% ;
}

.editor form {
  height: 100% ;
  margin-right: 50px ;
}

.textarea {
  resize: none ;
  outline: none ;
  width: 100% ;
  height: 100% ;
}

.popup {
  position: absolute ;
  display: flex ;
  top: 0 ;
  bottom: 0 ;
  left: 0 ;
  right: 0 ;
  flex-direction: column ;
  justify-content: center ;
  align-items: center ;
  background: rgba(169, 169, 169, 0.3) ;
}

.popup-content {
  display: flex ;
  flex-direction: column ;
  align-items: center ;
  background: #A9A9A9 ;
  padding: 5px 15px ;
  border-radius: 5px ;
  border: 1px solid #000 ;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.46) ;
}

.popup-content h1 {
  color: #890000 ;
}